// app/components/AttentionScatter.tsx
import { ScatterChart, Scatter, XAxis, YAxis, Tooltip, ResponsiveContainer } from "recharts";

export default function AttentionScatter({ data }: { data: any[] }) {
  return (
    <div className="h-80 w-full">
      <h2 className="text-xl font-semibold mb-2">Attention vs Performance</h2>
      <ResponsiveContainer width="100%" height="100%">
        <ScatterChart>
          <XAxis dataKey="attention" name="Attention" />
          <YAxis dataKey="assessment_score" name="Score" />
          <Tooltip />
          <Scatter data={data} fill="#82ca9d" />
        </ScatterChart>
      </ResponsiveContainer>
    </div>
  );
}
