"use client";
import { ResponsiveContainer, RadarChart, Radar, PolarGrid, PolarAngleAxis, PolarRadiusAxis, Legend } from "recharts";

export default function PersonaRadar({ personas }: { personas: any }) {
  if (!personas || Object.keys(personas).length === 0) return null;

  // Convert object to array
  const chartData = Object.entries(personas).map(([persona, skills]: [string, any]) => ({
    persona,
    ...skills,
  }));

  return (
    <div className="h-96 w-full">
      <h2 className="text-xl font-semibold mb-2">Persona Profiles</h2>
      <ResponsiveContainer width="100%" height="100%">
        <RadarChart data={chartData}>
          <PolarGrid />
          <PolarAngleAxis dataKey="persona" />
          <PolarRadiusAxis angle={30} domain={[0, 100]} />
          <Radar name="Comprehension" dataKey="comprehension" stroke="#8884d8" fill="#8884d8" fillOpacity={0.6} />
          <Radar name="Attention" dataKey="attention" stroke="#82ca9d" fill="#82ca9d" fillOpacity={0.6} />
          <Radar name="Focus" dataKey="focus" stroke="#ffc658" fill="#ffc658" fillOpacity={0.6} />
          <Radar name="Retention" dataKey="retention" stroke="#ff7300" fill="#ff7300" fillOpacity={0.6} />
          <Legend />
        </RadarChart>
      </ResponsiveContainer>
    </div>
  );
}
